.list-son{
    border: 0px green solid;
    border-radius: 10px;
    box-shadow: -10px -8px 10px 3px ;
    position: relative;
    padding: calc(2px + 2vmin) calc(1px + 3vmin);
    height: calc(50px + 5vmax);
    margin-top: calc(15px);
    display: flex;
    align-items: center;
    transition: transform ease 1000ms;
   


}

.list-son:hover{
    background-color: rgb(220, 104, 137);
    transform: scale(calc(0.08 + 1.05));
    margin: calc(30px) 0;
}

.list-son:last-child{
    margin-bottom: 10px;
  }
  

.btnCont{
    position: absolute;
    right: 10px;
}

.btn{
        font-size: calc(10px + 2vmin);
        margin-left: calc(10px + 2vmin);
        padding:calc(2px + 2vmin) calc(1px + 3vmin);

}

.form {
    width:100%;
    /* background-color: aquamarine; */
    display: flex;
    /* border: 2px green solid; */
    margin: calc(10px) 0;
    justify-content: space-between;
    height: calc(50px + 5vmax);


}

.editInput{
    width:calc(100px + 190%);
    padding-left: calc(10px);
    font-size: calc(10px + 2vmin);
    border-radius: 10px 0 0 10px;
}

.editInput:focus{
    border:5px rgb(183, 195, 46) solid;

}

.btn:hover{
    background-color: antiquewhite;
}