.disappear{
    display: none;
}

#hide{
    position: absolute;
    top: 300px;
    right:calc(13vw + 5vmax);
    border: 5px red solid;
    background-color: rgba(250, 234, 214,1);
    padding-top: 5vmax;
    padding-bottom: 5vmax;
    width: calc(50vw + 5vmax);
    max-height: calc(50vw + 5vmax);
    border-radius: 20px;

}
#hide p{
    font-size: calc(10px + 2vmin);
}
.errorbutton{
    padding: calc(5px + 2vmin) calc(50px + 2vmin);
    margin-top: calc(10px + 2vmin);
    font-size: calc(10px + 2vmin);
    border-radius: 10px;
    background-color: bisque;
    border: 0px ;


} 
.errorbutton:hover{
    background-color: rgb(234, 185, 112);
    cursor: pointer;
    
}
.errorbutton:focus{
    border: 1px rgb(191, 72, 51) solid;
    box-shadow: -2px 0px 10px rgb(183, 108, 34);

}