*{padding: 0;
}
.App {
  text-align: center;
  background-color: #a5172a;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App h2{
  font-size: calc(30px + 2vmin);
  margin: calc(100px) 0;
}


.App ul{
  /* background-color: blue; */
  list-style: none;
  text-align: left;
  font-size: calc(10px + 2vmin);
  text-indent: 0;
  text-indent: calc(10px);
  /* padding-left: 10px; */
  width: calc(200px + 40%);
  display: flex;
  flex-direction: column-reverse;/*to reverse list and start list with the last item is added*/
  
}

